





































import Vue from "vue";
import Component from "vue-class-component";
import ImgControlPanel from "@/components/ImgControls/ImgControlPanel.vue";
import { Prop, Watch } from "vue-property-decorator";
import { ShaderPass } from "three/examples/jsm/postprocessing/ShaderPass";
@Component({
  components: {
    ImgControlPanel
  }
})
export default class extends Vue {
  // min = 0;
  // max = 255;

  @Prop({
    required: true,
    default: ShaderPass
  })
  pass!: ShaderPass;

  @Prop({
    required: true
  })
  controls!: { min: 0; max: 255 };

  @Prop({
    type: String,
    required: true
  })
  readonly id!: string;

  @Prop({
    type: Boolean,
    required: false
  })
  readonly upDisabled!: boolean;

  @Prop({
    type: Boolean,
    required: false
  })
  readonly downDisabled!: boolean;

  @Watch("controls.min")
  minChanged(newVal: number) {
    this.pass.uniforms.uMin.value = newVal / 256;
    this.$emit("change");
  }

  @Watch("controls.max")
  maxChanged(newVal: number) {
    this.pass.uniforms.uMax.value = newVal / 256;
    this.$emit("change");
  }

  reset() {
    this.controls.min = 0;
    this.controls.max = 255;
  }
}
