







































































import { ShaderPass } from "three/examples/jsm/postprocessing/ShaderPass";
import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";

@Component
export default class ImgControlPanel extends Vue {
  name = "ImgControlPanel";

  @Prop({
    required: true,
    default: ShaderPass
  })
  pass!: ShaderPass;

  @Prop({
    type: String,
    required: true
  })
  readonly id!: string;

  @Prop({
    type: Function,
    required: true,
    default: () => {
      return;
    }
  })
  readonly reset!: Function;

  @Prop({
    type: String,
    required: true,
    default: ""
  })
  readonly title!: string;

  @Prop({
    type: Boolean,
    required: false
  })
  readonly upDisabled!: boolean;

  @Prop({
    type: Boolean,
    required: false
  })
  readonly downDisabled!: boolean;

  oneUpMe() {
    this.$emit("oneUp", this.id);
  }

  oneDownMe() {
    this.$emit("oneDown", this.id);
  }

  removeMe() {
    this.$emit("remove", this.id);
  }
}
