

























import Vue from "vue";
import Component from "vue-class-component";
import ImgControlPanel from "@/components/ImgControls/ImgControlPanel.vue";
import { Prop, Watch } from "vue-property-decorator";
import { ShaderPass } from "three/examples/jsm/postprocessing/ShaderPass";
@Component({
  components: {
    ImgControlPanel
  }
})
export default class extends Vue {
  // threshold = 128;

  @Prop({
    required: true,
    default: ShaderPass
  })
  pass!: ShaderPass;

  @Prop({
    required: true
  })
  controls!: { threshold: 128 };

  @Prop({
    type: String,
    required: true
  })
  readonly id!: string;

  @Prop({
    type: Boolean,
    required: false
  })
  readonly upDisabled!: boolean;

  @Prop({
    type: Boolean,
    required: false
  })
  readonly downDisabled!: boolean;

  @Watch("controls.threshold")
  thresholdChanged(newVal: number) {
    this.pass.uniforms.uThreshold.value = newVal / 256;
  }

  reset() {
    this.controls.threshold = 128;
  }
}
