

























import Vue from "vue";
import Component from "vue-class-component";
import ImgControlPanel from "@/components/ImgControls/ImgControlPanel.vue";
import { Prop, Watch } from "vue-property-decorator";
import { ShaderPass } from "three/examples/jsm/postprocessing/ShaderPass";
@Component({
  components: {
    ImgControlPanel
  }
})
export default class extends Vue {
  // brightness = 0;

  @Prop({
    required: true,
    default: ShaderPass
  })
  pass!: ShaderPass;

  @Prop({
    required: true
  })
  controls!: { brightness: 0 };

  @Prop({
    type: String,
    required: true
  })
  readonly id!: string;

  @Prop({
    type: Boolean,
    required: false
  })
  readonly upDisabled!: boolean;

  @Prop({
    type: Boolean,
    required: false
  })
  readonly downDisabled!: boolean;

  @Watch("controls.brightness")
  brightnessChanged(newVal: number) {
    this.pass.uniforms.uBrightness.value = newVal / 256;
  }

  reset() {
    this.controls.brightness = 0;
  }
}
