





































import Vue from "vue";
import Component from "vue-class-component";
import ImgControlPanel from "@/components/ImgControls/ImgControlPanel.vue";
import { Prop, Watch } from "vue-property-decorator";
import { ShaderPass } from "three/examples/jsm/postprocessing/ShaderPass";
@Component({
  components: {
    ImgControlPanel
  }
})
export default class extends Vue {
  // contrast = 0;
  // reference = 128;

  @Prop({
    required: true,
    default: ShaderPass
  })
  pass!: ShaderPass;

  @Prop({
    required: true
  })
  controls!: {
    contrast: 0;
    reference: 128;
  };

  @Prop({
    type: String,
    required: true
  })
  readonly id!: string;

  @Prop({
    type: Boolean,
    required: false
  })
  readonly upDisabled!: boolean;

  @Prop({
    type: Boolean,
    required: false
  })
  readonly downDisabled!: boolean;

  @Watch("controls.contrast")
  contrastChanged(newVal: number) {
    this.pass.uniforms.uContrast.value =
      (newVal >= 0 ? newVal / 25 : newVal / 50) + 1;
  }

  @Watch("controls.reference")
  referenceChanged(newVal: number) {
    this.pass.uniforms.uReference.value = newVal / 256;
  }

  reset() {
    this.controls.contrast = 0;
    this.controls.reference = 128;
  }
}
