const ThresholdShader = {
  uniforms: {
    tDiffuse: {
      value: null
    },
    uThreshold: {
      value: 0.5
    }
  },
  vertexShader: `
    varying vec2 vUv;
    void main () {
      vUv = uv;
      gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
    }
  `,
  fragmentShader: `
    varying vec2 vUv;
    uniform sampler2D tDiffuse;
    uniform mediump float uThreshold;
    
    void main(void) {
      gl_FragColor = texture2D(tDiffuse, vUv);
      gl_FragColor.r < uThreshold ? gl_FragColor.r = 0. : gl_FragColor.r = 1.;
      gl_FragColor.g < uThreshold ? gl_FragColor.g = 0. : gl_FragColor.g = 1.;
      gl_FragColor.b < uThreshold ? gl_FragColor.b = 0. : gl_FragColor.b = 1.;
    }
  `
};

const ContrastShader = {
  uniforms: {
    tDiffuse: {
      value: null
    },
    uContrast: {
      value: 1
    },
    uReference: {
      value: 0.5
    }
  },
  vertexShader: `
    varying vec2 vUv;
    void main () {
      vUv = uv;
      gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
    }
  `,
  fragmentShader: `
    varying vec2 vUv;
    uniform sampler2D tDiffuse;
    uniform mediump float uContrast;
    uniform mediump float uReference;

    void main(void) {
      vec4 color = texture2D(tDiffuse, vUv);
      color = (color - uReference) * uContrast + uReference;
      gl_FragColor = clamp(color, 0.0, 1.0);
    }
  `
};

const BrightnessShader = {
  uniforms: {
    tDiffuse: {
      value: null
    },
    uBrightness: {
      value: 0
    }
  },
  vertexShader: `
    varying vec2 vUv;
    void main () {
      vUv = uv;
      gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
    }
  `,
  fragmentShader: `
    varying vec2 vUv;
    uniform sampler2D tDiffuse;
    uniform mediump float uBrightness;
    
    void main(void) {
      gl_FragColor = texture2D(tDiffuse, vUv) + uBrightness;
    }
  `
};

const GammaShader = {
  uniforms: {
    tDiffuse: {
      value: null
    },
    uGamma: {
      value: 3
    }
  },
  vertexShader: `
    varying vec2 vUv;
    void main () {
      vUv = uv;
      gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
    }
  `,
  fragmentShader: `
    varying vec2 vUv;
    uniform sampler2D tDiffuse;
    uniform mediump float uGamma;
    
    void main(void) {
      gl_FragColor = texture2D(tDiffuse, vUv);
      gl_FragColor = vec4(pow(gl_FragColor.rgb, vec3(uGamma)), 1.0);
    }
  `
};

const QuantizationShader = {
  uniforms: {
    tDiffuse: {
      value: null
    },
    uQuantization: {
      value: 1 / 15
    }
  },
  vertexShader: `
    varying vec2 vUv;
    void main () {
      vUv = uv;
      gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
    }
  `,
  fragmentShader: `
    varying vec2 vUv;
    uniform sampler2D tDiffuse;
    uniform mediump float uQuantization;
    
    void main(void) {
      gl_FragColor = texture2D(tDiffuse, vUv);
      gl_FragColor = (uQuantization) * floor(gl_FragColor / uQuantization);
    }
  `
};

const HistogramStretchShader = {
  uniforms: {
    tDiffuse: {
      value: null
    },
    uMin: {
      value: 0.0
    },
    uMax: {
      value: 1.0
    }
  },
  vertexShader: `
    varying vec2 vUv;
    void main () {
      vUv = uv;
      gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
    }
  `,
  fragmentShader: `
    varying vec2 vUv;
    uniform sampler2D tDiffuse;
    uniform mediump float uMin;
    uniform mediump float uMax;
    
    void main(void) {
      gl_FragColor = texture2D(tDiffuse, vUv);
      gl_FragColor = (gl_FragColor - uMin)/(uMax - uMin);
    }
  `
};

const TransformShader = {
  uniforms: {
    tDiffuse: {
      value: null
    },
    uMode: {
      value: 0.0
    },
    uWidth: {
      value: 1.0
    }
  },
  vertexShader: `
    varying vec2 vUv;
    void main () {
      vUv = uv;
      gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
    }
  `,
  fragmentShader: `
    varying vec2 vUv;
    uniform sampler2D tDiffuse;
    uniform mediump float uMode;
    uniform mediump float uWidth;
    
    void main(void) {
      float PI = 3.14159265358979323844;
      if(uMode == 0.0){
        gl_FragColor = texture2D(tDiffuse, vUv);
      } // For Polar
      else if(uMode == 1.0){
        vec2  point = vUv;
        // Cast (0,1) -> (0,2PI)
        float theta = (point.y * 2.* PI);
        // Cast (0,1) -> (0, sqrt(.5))
        float distance = point.x * sqrt(.5);
        vec2  polar = vec2(distance * cos(theta) + .5, .5 - distance * sin(theta));
        gl_FragColor = texture2D(tDiffuse, polar);
      } // For Log-Polar
      else{
        vec2 point  = vUv;
        // Cast (0,1) -> (0,2PI)
        float theta = (point.y * 2. * PI);
        float distance = log(1. - point.x)/log(sqrt(uWidth)) * sqrt(.5);
        vec2  polar = vec2(distance * cos(theta) + .5, distance * sin(theta) + .5);
        gl_FragColor = texture2D(tDiffuse, polar);
      }
    }
  `
};

const BlurShader = {
  uniforms: {
    tDiffuse: {
      value: null
    },
    uFactor: {
      value: 1.0 / 1.0
    },
    uX: {
      value: (1.0 - 1.0) / 2.0 / 512.0
    },
    uY: {
      value: (1.0 - 1.0) / 2.0 / 512.0
    }
  },
  vertexShader: `
    varying vec2 vUv;
    void main () {
      vUv = uv;
      gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
    }
  `,
  fragmentShader: `
    varying vec2 vUv;
    uniform sampler2D tDiffuse;
    uniform float uFactor;
    uniform float uX;
    uniform float uY;

    void main (void) {
      vec4 colorsum = vec4(0);
      for (float i = -uY; i <= uY; i += 1.0/512.0){
        for (float j = -uX; j <= uX; j += 1.0/512.0){
          colorsum += texture2D(tDiffuse, vUv + vec2(j, i));
        }
      }
      gl_FragColor = vec4((colorsum * uFactor).rgb, 1.0);
    }
  `
};

const SharpenShader = {
  uniforms: {
    tDiffuse: {
      value: null
    },
    uFactor: {
      value: 1.0 / 1.0
    },
    uX: {
      value: (1.0 - 1.0) / 2.0 / 512.0
    },
    uY: {
      value: (1.0 - 1.0) / 2.0 / 512.0
    },
    uSize: {
      value: 1.0 / 1.0
    }
  },
  vertexShader: `
    varying vec2 vUv;
    void main () {
      vUv = uv;
      gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
    }
  `,
  fragmentShader: `
    varying vec2 vUv;
    uniform sampler2D tDiffuse;
    uniform float uFactor;
    uniform float uSize;
    uniform float uX;
    uniform float uY;

    void main (void) {
      vec4 colorsum = vec4(0);
      for (float i = -uY; i <= uY; i += 1.0/512.0){
        for (float j = -uX; j <= uX; j += 1.0/512.0){
          colorsum += texture2D(tDiffuse, vUv + vec2(j, i));
        }
      }
      colorsum = vec4((colorsum * uSize).rgb, 1.0);
      vec4 original = texture2D(tDiffuse, vUv);
      vec4 tmp = uFactor * (original - colorsum);
      tmp += original;
      clamp(tmp, 0.0, 1.0);
      gl_FragColor = vec4(tmp.rgb, 1.0);
    }
  `
};

import { Shader } from "three";

const Shaders: { [key: string]: Shader } = {
  ThresholdShader: ThresholdShader,
  ContrastShader: ContrastShader,
  BrightnessShader: BrightnessShader,
  GammaShader: GammaShader,
  QuantizationShader: QuantizationShader,
  HistogramStretchShader: HistogramStretchShader,
  TransformShader: TransformShader,
  BlurShader: BlurShader,
  SharpenShader: SharpenShader
};

export { Shaders };
