




















import Vue from "vue";
import Component from "vue-class-component";
import ImgControlPanel from "@/components/ImgControls/ImgControlPanel.vue";
import { Prop, Watch } from "vue-property-decorator";
import { ShaderPass } from "three/examples/jsm/postprocessing/ShaderPass";
@Component({
  components: {
    ImgControlPanel
  }
})
export default class extends Vue {
  transformItems: string[] = ["Cartesian", "Polar", "Log Polar"];
  // transform = "Cartesian";

  @Prop({
    required: true,
    default: ShaderPass
  })
  pass!: ShaderPass;

  @Prop({
    required: true
  })
  controls!: { transform: "Cartesian" };

  @Prop({
    type: String,
    required: true
  })
  readonly id!: string;

  @Prop({
    type: Boolean,
    required: false
  })
  readonly upDisabled!: boolean;

  @Prop({
    type: Boolean,
    required: false
  })
  readonly downDisabled!: boolean;

  @Watch("controls.transform")
  transformChanged(newVal: string) {
    this.pass.uniforms.uMode.value =
      newVal == "Cartesian" ? 0.0 : newVal == "Polar" ? 1.0 : 2.0;
    this.pass.uniforms.uWidth.value = 512;
  }

  reset() {
    this.controls.transform = "Cartesian";
  }
}
