

















































import Vue from "vue";
import Component from "vue-class-component";
import ImgControlPanel from "@/components/ImgControls/ImgControlPanel.vue";
import { Prop, Watch } from "vue-property-decorator";
import { ShaderPass } from "three/examples/jsm/postprocessing/ShaderPass";
@Component({
  components: {
    ImgControlPanel
  }
})
export default class extends Vue {
  // horizontal = 1;
  // vertical = 1;
  // factor = 1;

  @Prop({
    required: true,
    default: ShaderPass
  })
  pass!: ShaderPass;

  @Prop({
    required: true
  })
  controls!: {
    horizontal: 1;
    vertical: 1;
    factor: 1;
  };

  @Prop({
    type: String,
    required: true
  })
  readonly id!: string;

  @Prop({
    type: Boolean,
    required: false
  })
  readonly upDisabled!: boolean;

  @Prop({
    type: Boolean,
    required: false
  })
  readonly downDisabled!: boolean;

  @Watch("controls.horizontal")
  horizontalChanged(newVal: number) {
    const other = this.controls.vertical;
    this.pass.uniforms.uSize.value = 1 / (newVal * other);
    this.pass.uniforms.uX.value = (newVal - 1.0) / 2.0 / 512.0;
  }

  @Watch("controls.vertical")
  verticalChanged(newVal: number) {
    const other = this.controls.horizontal;
    this.pass.uniforms.uSize.value = 1 / (newVal * other);
    this.pass.uniforms.uY.value = (newVal - 1.0) / 2.0 / 512.0;
  }

  @Watch("controls.factor")
  factorChanged(newVal: number) {
    this.pass.uniforms.uFactor.value = newVal;
  }

  reset() {
    this.controls.horizontal = 1;
    this.controls.vertical = 1;
    this.controls.factor = 1;
  }
}
