

























import Vue from "vue";
import Component from "vue-class-component";
import ImgControlPanel from "@/components/ImgControls/ImgControlPanel.vue";
import { Prop, Watch } from "vue-property-decorator";
import { ShaderPass } from "three/examples/jsm/postprocessing/ShaderPass";
@Component({
  components: {
    ImgControlPanel
  }
})
export default class extends Vue {
  // gamma = 3;

  @Prop({
    required: true,
    default: ShaderPass
  })
  pass!: ShaderPass;

  @Prop({
    required: true
  })
  controls!: { gamma: 3 };

  @Prop({
    type: String,
    required: true
  })
  readonly id!: string;

  @Prop({
    type: Boolean,
    required: false
  })
  readonly upDisabled!: boolean;

  @Prop({
    type: Boolean,
    required: false
  })
  readonly downDisabled!: boolean;

  @Watch("controls.gamma")
  gammaChanged(newVal: number) {
    this.pass.uniforms.uGamma.value = newVal;
  }

  reset() {
    this.controls.gamma = 3;
  }
}
